.main-title {
	margin: 0px 0px 16px 0px;
	font-weight: 500;

	line-height: 48px;
	color: #091133;
	transform: translate(0px, 200px);
	opacity: 0;
	transition: all 0.7s ease 0s;
	animation: name 4s infinite linear;
	&._active {
		transform: translate(0px, 0px);
		opacity: 1;
	}
	@media (max-width: $pc) {
		font-size: 36px;
	}
	@media (max-width: $tablet) {
		font-size: 25px;
	}
	@media (max-width: $mobile) {
		font-size: 20px;
	}
}
